import React from "react";
import {HeroIcon,HeroPackSelected} from "./index";
import {Box, Grid, Stack} from "@mui/material";
import {HeroData} from "../types";
import {IHeroPack} from "../types/IHeroPack";

interface IProps {
    onFilterCounterHero: any,
    heroCounterFilter: number[],
    mainHeroPack?: IHeroPack,
    mainHeroPackType?: string,
    heroLostPacks: IHeroPack[],
    emptyMessage: string,
}

interface IState {

}

export class HeroCounterPackGrid extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {};
    }

    renderOneHeroPack(heroPack: IHeroPack) {

        const items = []
        const hl=heroPack.hIds.length
        const newHeropack:any[]=[]
        for (let i=0;i<5 && i<hl;i++) {
            const hero = HeroData.getHeroById(~~heroPack.hIds[i])
            if (hero) {
                newHeropack.push({heroId:hero.heroId,battleOrder:hero.battleOrder,heroName:hero.heroName})
            }
        }
        newHeropack.sort((a,b) => {
            return a.heroName>b.heroName?1:-1
        })
        for (const i in newHeropack) {
            const hero=newHeropack[i]
                    items.push(<HeroIcon heroId={hero.heroId} renderType={20} heroProb={-1}
                                     isSelected={false}
                                     OnClick={this.props.onFilterCounterHero} key={hero.heroId}/>)

        }
        return items
    }


    renderHeroPacks() {
        const items = []
        let cnt=0
        for (const j in this.props.heroLostPacks) {

            const heroPackFull=this.props.heroLostPacks[j]
            const heroPack=heroPackFull.hIds
            if (this.props.heroCounterFilter.indexOf(heroPack[0])<0 && this.props.heroCounterFilter.indexOf(heroPack[1])<0 && this.props.heroCounterFilter.indexOf(heroPack[2])<0 && this.props.heroCounterFilter.indexOf(heroPack[3])<0 &&this.props.heroCounterFilter.indexOf(heroPack[4])<0)
            /*items.push(<Stack direction="row" key={cnt++} sx={{
                bgcolor: 'primary.light',
                m:0.25
            }}>
                {this.renderOneHeroPack(heroPack)}
            </Stack>)*/
                items.push(<HeroPackSelected  key={heroPack.join(',')+this.props.mainHeroPack?.hIds?.join(',')+this.props.mainHeroPackType} heroIds={heroPackFull} heroIdsA={this.props.mainHeroPack?(this.props.mainHeroPackType==='a'?this.props.mainHeroPack:heroPackFull):undefined } heroIdsD={this.props.mainHeroPack?(this.props.mainHeroPackType==='d'?this.props.mainHeroPack:heroPackFull):undefined } deselectHero={this.props.onFilterCounterHero}/>)
        }
        if (items.length===0) {
            items.push(
                <Stack direction="row" key={cnt++} sx={{
                    bgcolor: 'primary.light',
                    m:0.25
                }}>{this.props.emptyMessage}</Stack>
            )
        }
        return items
    }

    render() {
        //console.log('RHCPG',this.props.heroLostPacks)
        return (<Stack sx={{
            margin: 1,
            bgcolor: 'primary.dark',

        }}>
            {this.renderHeroPacks()}
        </Stack>)
    }
}

export default HeroCounterPackGrid