import React from "react";
import {HeroGrid, HeroPackSelected} from "./index";
import {Box, Button, Chip, Container, Divider} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {MyNavigator} from "../types/MyNavigator";


interface IProps {
    heroIdsA: IHeroPack
    navigateFunction: any
}

interface IState {
    heroIdsA: IHeroPack,
    displayEditA: boolean,
    requestsInProgress: number
}

export class HeroAddDefenderMode extends React.Component<IProps, IState> {
    static emptyMap=new Map<number,number>()
    constructor(props:any) {
        super(props);
        this.state = {
            heroIdsA: props.heroIdsA,
            displayEditA: props.heroIdsA.length!==5,
            requestsInProgress: 0
        };
        this.selectHeroA=this.selectHeroA.bind(this)
        this.deselectHeroA=this.deselectHeroA.bind(this)
        this.toggleEditA=this.toggleEditA.bind(this)
        this.sendToQueue=this.sendToQueue.bind(this)
        this.setHeroPack=this.setHeroPack.bind(this)
        MyNavigator.heroAddDefenderMode=this
    }

    sendToQueue() {
        const body:any={
            d1:-1,
            d2:-1,
            d3:-1,
            d4:-1,
            d5:-1,
        }
        for (let i=0;i<5 && i<this.state.heroIdsA.hIds.length;i++) {body["d"+(i+1)]=this.state.heroIdsA.hIds[i];}
        const state=this.state; this.setState({requestsInProgress:state.requestsInProgress+1})
        fetch("https://g92e0914be8be53-db40vlv.adb.eu-frankfurt-1.oraclecloudapps.com/ords/hwstat/hws/queueRD/",{
            method: "POST",
            headers: {
                "Content-type": "application/json"
            },
            body: JSON.stringify(body)}).then(()=>{
                console.log("Good")
                this.props.navigateFunction(3)
        }).finally(()=>{
            const state=this.state; this.setState({requestsInProgress:state.requestsInProgress-1})
        })
    }

    setHeroPack(heroPack:IHeroPack) {
        this.setState({heroIdsA:heroPack,displayEditA:heroPack.hIds.length!==5})
    }

    selectHeroA(e:any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsA.hIds
            if (!heroIds.includes(heroId) && heroIds.length < 5) {
                heroIds.push(heroId)
                this.setState({heroIdsA:{hIds:heroIds} })
            }
        }
    }

    deselectHeroA(e: any) {
        if (e && e.target && e.target.dataset && e.target.dataset.heroid) {
            const heroId = ~~e.target.dataset.heroid
            const heroIds = this.state.heroIdsA.hIds
            const index = heroIds.indexOf(heroId, 0)
            if (index > -1) {
                heroIds.splice(index, 1);
                this.setState({heroIdsA:{hIds:heroIds}})
            }
        }
    }

    toggleEditA() {
        const newState=!this.state.displayEditA
        this.setState({displayEditA:newState})
    }

    renderA() {
        if (this.state.displayEditA) {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Защищающийся пак"} /></Divider>
                <HeroPackSelected heroIds={this.state.heroIdsA} deselectHero={this.deselectHeroA}/>
                {this.state.heroIdsA.hIds.length>0?
                    <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditA}>Готово</Button>
                    :
                    null}
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Выбери защищающихся героев"} /></Divider>
                <HeroGrid showAll={false} heroIds={this.state.heroIdsA} heroProbs={HeroAddDefenderMode.emptyMap}
                          selectHero={this.selectHeroA}/>
            </Box>)
        } else {
            return (<Box  className={"heroADBox"}>
                <Divider><Chip label={this.state.requestsInProgress>0?"Идет загрузка...просто жди!":"Защищающийся пак"} /></Divider>
                <HeroPackSelected heroIds={this.state.heroIdsA} deselectHero={this.deselectHeroA}/>
                <Button variant="outlined" className={"heroEditPackButton"} onClick={this.toggleEditA}>Изменить</Button>
            </Box>)
        }
    }


    renderSendButton() {
        if (this.state.heroIdsA.hIds.length>0 && !this.state.displayEditA) {
            return (<Button variant="contained" onClick={this.sendToQueue} sx={{
                marginTop: '1em',
                maxWidth: 'min-content'
            }}>Отправить</Button>)
        } else {
            return (<Button disabled>Выбери героев перед отправкой</Button>)
        }
    }

    render() {
        return (<Box sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center'
        }}>
            {this.renderA()}
            {this.renderSendButton()}
        </Box>)
    }
}

export default HeroAddDefenderMode;